import jsYaml from 'js-yaml'
import {BaseND, BaseStruct, BaseType, KindPoint, Point, RootPoint} from "@/service/base";
import baseData from "@/data/base";

let id: number = 1

export default class Basic {

    /**
     * 获取唯一id
     * @return number
     */
    public static getId(): number {
        return id++
    }

    /**
     * 获取root节点
     * @return Point
     */
    public static getRootPoint(): Point {
        return {
            id: this.getId(),
            name: RootPoint,
            label: RootPoint,
            desc: '基础节点，无特殊作用',
            type: BaseType.Object,
            mark: '',
            children: []
        }
    }

    /**
     * 获取k8s基础根节点
     * @return Point[]
     */
    public static getK8sRootPoints(): Point[] {
        return Basic.baseToPoint(baseData['baseK8s'])
    }

    /**
     * 获取k8s基础节点，无根节点
     * @return Point[]
     */
    public static getK8sNoRootPoints(): Point[] {
        const data = this.getK8sRootPoints()
        for (const v of data) {
            if (v.name === RootPoint) {
                return v.children
            }
        }
        return []
    }

    /**
     * 获取资源节点
     * @param name
     * @return Point[]
     */
    public static getResourcePoints(name: string): Point[] {
        const kindData = baseData[name]
        return kindData ? Basic.baseToPoint(kindData) : []
    }

    /**
     * 基础结构转树节点结构
     * @param base
     * @return Point[]
     */
    public static baseToPoint(base: BaseStruct[]): Point[] {
        let pointSet: Point[] = []
        for (const v of base) {
            // 判断是否必须存在，否则归入提示集合
            const point: Point = this.buildPoint(v)
            pointSet.push(point)
        }
        return JSON.parse(JSON.stringify(pointSet))
    }

    /**
     * 构建树节点
     * @param data
     * @return Point
     */
    private static buildPoint(data: BaseStruct): Point {
        let label: string = data.name
        let value: string = data.value || ''
        let mark: string = ' '
        let keyTips: Point[] = []
        let children: Point[] = []
        switch (data.type) {
            case BaseType.String || BaseType.Number || BaseType.Boolean:
                mark = data.name !== '-' ? ": " : mark
                break
            // case BaseType.Array:
            //     // 数组自动添加 - 节点，考虑子项类型
            //     child = this.addArrayPoint(data)
            //     keyTips = [child]
            //     break
            default: // 其它都以Object处理
                value = ''
                if (data.items) {
                    keyTips = this.baseToPoint(data.items)
                }
                break
        }
        value !== "" ? label += mark + value : null
        for (const tip of keyTips) {
            if (tip.must) {
                children.push(tip)
            }
        }
        return {
            arrayType: data.arrayType,
            children: children,
            desc: data.desc,
            id: this.getId(),
            name: data.name,
            label: label,
            must: data.must || false,
            tips: data.tips || [],
            type: data.type || BaseType.Object,
            value: value,
            mark: mark,
            items: data.items
        }
    }

    /**
     * 添加数组节点
     * @param arrayPoint
     * @deprecated
     */
    private static addArrayPoint(arrayPoint: BaseStruct): Point {
        let arrayType = arrayPoint.arrayType
        if (!arrayType) {
            if (arrayPoint.items && arrayPoint.items.length > 0) {
                arrayType = BaseType.Object
            }
        }
        const data: Point = {
            id: this.getId(),
            desc: arrayPoint.desc,
            name: '-',
            label: '-',
            tips: arrayPoint.tips || [],
            children: [],
            type: arrayType || BaseType.String,
            value: "", mark: ""
        }
        let child: Point | null = null
        let children: Point[] = []
        let keyTips: Point[] = []
        switch (data.type) {
            case BaseType.Object:
                keyTips = this.baseToPoint(arrayPoint.items || [])
                break
            case BaseType.Array:
                child = this.addArrayPoint(data)
                keyTips = [child]
                break
            default:
                break
        }
        for (const tip of keyTips) {
            if (tip.must) {
                children.push(tip)
            }
        }
        data.children = children
        return data
    }

    // 节点添加子节点
    public static append(tag: Point, childTagType: BaseType, childTagName: string): Point {
        const id = this.getId()
        let newChild: Point = {
            id: id,
            type: childTagType,
            label: childTagName,
            name: childTagName,
            children: [],
            desc: '', mark: '', value: '',
        }
        const items = tag.items
        if (items) {
            for (const v of items) {
                if (childTagName === v.name) {
                    newChild = this.buildPoint(v)
                    break
                }
            }
        }
        tag.children.push(newChild)
        return tag
    }

    // 节点添加key提示
    public static setKeyTip(source: BaseStruct[]): BaseND[] {
        let renderData: BaseND[] = []
        for (const s of source) {
            renderData.push({
                name: s.name,
                desc: s.desc
            })
        }
        return renderData
    }

    // 节点添加值提示
    public static setValueTip(source: BaseND[]) {
        return source
    }

    /**
     * 树结构转Yaml
     * @param data
     * @param level
     * @param mark
     * @deprecated
     */
    public static convertToYamlOld(data: Point[], level: number = 0, mark: boolean = false): string {
        let str: string = ''
        let blank: string = ''
        for (let i = 0; i < level; i++) {
            blank += '  '
        }
        level++
        const len = data.length
        for (let i = 0; i < len; i++) {
            const v = data[i]
            !mark || i > 0 ? str += blank : null
            let symbol = ':'
            if (v.name === '-') {
                symbol = ''
                mark = v.children && v.children.length > 0;
            } else {
                mark = false
            }
            str += v.name + symbol + ' ' + (v.value || '')
            if (!mark) {
                str += `\n`
            }
            str += this.convertToYamlOld(v.children, level, mark)
        }
        return str
    }

    /**
     * 树结构转Yaml
     * @param data
     * @return string
     */
    public static convertToYaml(data: Point[]): string {
        const obj = this.convertToObject(data)
        return jsYaml.safeDump(obj)
    }

    /**
     * 树结构转Object
     * @param data
     * @return any
     */
    public static convertToObject(data: Point[]): any {
        let obj: any = {}
        for (const v of data) {
            if (v.name === RootPoint) {
                obj = this.convertToObject(v.children)
                break
            }
            if (v.type === BaseType.Array) {
                obj[v.name] = this.convertToArray(v.children)
                continue
            }
            if (v.type === BaseType.Object) {
                obj[v.name] = this.convertToObject(v.children)
                continue
            }
            obj[v.name] = v.value || ''
        }
        return obj
    }

    /**
     * 树结构转数组
     * @param data
     * @return any[]
     */
    private static convertToArray(data: Point[]): any[] {
        let arr: any[] = []
        for (const v of data) {
            if (v.name !== '-' || v.type === BaseType.Array) {
                continue
            }
            if (v.type === BaseType.Object) {
                arr.push(this.convertToObject(v.children))
                continue
            }
            arr.push(v.value || '')
        }
        return arr
    }

    /**
     * 树结构获取kind类型
     */
    public static getTreeKind(data: Point[]): string {
        let kind = ''
        for (const v of data) {
            if (v.name === RootPoint) {
                kind = this.getTreeKind(v.children)
                break
            }
            if (v.name === KindPoint) {
                kind = v.value || ''
                break
            }
        }
        return kind
    }

    /**
     * 单Yaml转树结构
     * @param str
     * @return Point[]
     */
    public static convertToTree(str: string): Point[] {
        const data = this.objToTree(jsYaml.safeLoad(str))
        this.autoComplete(data)
        return data
    }

    /**
     * 多Yaml转树结构
     * @param str
     * @return Point[]
     */
    public static convertToTreeMulti(str: string): Point[] {
        const multiYamlObj = jsYaml.safeLoadAll(str)
        const data: Point[] = []
        for (const v of multiYamlObj) {
            const root = this.getRootPoint()
            root.children = this.objToTree(v)
            this.autoComplete(root.children)
            data.push(root)
        }
        return data
    }

    /**
     * Object转树结构
     * @param obj
     * @return Point[]
     */
    private static objToTree(obj: any): Point[] {
        const data: Point[] = []
        for (const index in obj) {
            if (!obj.hasOwnProperty(index)) {
                continue
            }
            const point: Point = { id: this.getId(), name: index, label: index, mark: ' ', desc: '', children: [] }
            const value = obj[index]
            if (typeof value === BaseType.Object && value) {
                if (Array.isArray(value)) {
                    point.type = BaseType.Array
                    point.children = this.arrayToTree(value)
                } else {
                    point.type = BaseType.Object
                    point.children = this.objToTree(value)
                }
            } else {
                point.value = value || ''
                point.mark = ': '
                point.type = BaseType.String
                point.label = point.name + point.mark + point.value
            }
            data.push(point)
        }
        return data
    }

    /**
     * 数组转树结构
     * @param arr
     * @return Point[]
     */
    private static arrayToTree(arr: any): Point[] {
        const data: Point[] = []
        for (const v of arr) {
            const point: Point = { id: this.getId(), name: '-', label: '-', mark: ' ', desc: '', children: [] }
            if (typeof v === BaseType.Object) {
                if (Array.isArray(v)) {
                    point.type = BaseType.Array
                    point.children = this.arrayToTree(v)
                } else {
                    point.type = BaseType.Object
                    point.children = this.objToTree(v)
                }
            } else {
                point.value = v || ''
                point.type = BaseType.String
                point.label = point.name + point.mark + point.value
            }
            data.push(point)
        }
        return data
    }

    public static autoComplete(data: Point[]): Point[] {
        for (const v of data) {
            if (v.name === RootPoint) {
                data = this.autoComplete(v.children)
                break
            }
            if (v.name === KindPoint) {
                const baseSet = baseData[v.value || '']
                if (baseSet && baseSet.length > 0) {
                    // 将基础结构的相应信息挂载到树节点上
                    data = this.execComplete(data, baseSet)
                }
                break
            }
        }
        return data
    }

    private static execComplete(data: Point[], baseSet: BaseStruct[]): Point[] {
        for (const v of data) {
            for (const bv of baseSet) {
                if (v.name === bv.name) {
                    v.desc = bv.desc
                    v.items = bv.items
                    v.tips = bv.tips
                    v.type = bv.type
                    v.arrayType = bv.arrayType
                    v.must = bv.must
                    if (v.items) {
                        v.children = this.execComplete(v.children, v.items)
                    }
                }
            }
        }
        return data
    }
}
