import {Component, Vue} from "vue-property-decorator"
import {BaseND, BaseStruct, BaseType, KindPoint, Point, RootPoint} from "@/service/base";
import {TreeNode} from "element-ui/types/tree";
import Basic from "@/service/basic"

// TODO 添加节点时，增加根据描述内容进行模糊筛选
// TODO 添加多个基础节点
@Component
export default class Template extends Vue {
    // yaml文本内容
    private yamlData: string = ''
    // 树节点总内容
    private data: Point[] = [Basic.getRootPoint()]
    // 创建子节点时，选择的节点名称
    private superTagName: string = ''
    // 创建的子节点名称
    private newTagName: string = ''
    // 选择的node
    private chooseNode: TreeNode<number, Point> | null = null
    // 选择的节点
    private chooseTag: Point | null = null
    // 添加节点的Dialog显示控制
    private dialogFormVisible: boolean = false
    // 创建的子节点类型
    private tagType: BaseType = BaseType.Object
    // 节点类型是否可选择
    private tagTypeDisable: boolean = false
    // 节点名称提示
    private restaurants: BaseND[] = []
    // 节点值提示
    private setValueRestaurants: BaseND[] = []
    // 节点值
    private tagValue: string = ''
    // 节点值输入框标题
    private tagTitle: string = '请填写Tag值'
    // 节点值Dialog显示控制
    private tagDialog: boolean = false
    // 数组节点类型Dialog显示控制
    private arrayTypeChooseDialog: boolean = false
    // 数组节点类型
    private arrTagType: BaseType = BaseType.String
    // 选择的kind模板类型
    private kindType: string = ''

    constructor() {
        super()
        this.data = Basic.getK8sRootPoints()
    }

    private appendShow(data: Point): boolean {
        return data.type === BaseType.Object || (data.type === BaseType.Array && !!data.arrayType)
    }

    private setValueShow(data: Point): boolean {
        const typeCheck = data.type !== BaseType.Object && data.type !== BaseType.Array
        const arrayCheck = data.name === '-' && (!data.items || data.items.length === 0) && !!data.arrayType && data.arrayType !== BaseType.Object
        return typeCheck || arrayCheck
    }

    private append(data: Point): void {
        this.superTagName = data.label
        this.chooseTag = data
        // 添加key提示信息
        this.restaurants = Basic.setKeyTip(data.items || [])
        // 当类型为array时自动confirm, 添加时自动添加一个array节点符 - ，在节点符上可再对该array内进行参数添加
        if (data.type === BaseType.Array) {
            return this.appendConfirm()
        }
        this.dialogFormVisible = true
    }

    private appendConfirm(skip: boolean = false): void {
        let tag = this.chooseTag
        if (tag !== null) {
            const childTagName = BaseType.Array === tag.type ? '-' : this.newTagName
            // 校验添加的节点名称是否为空
            if (childTagName === '' && !skip) {
                return this.warn('节点名称不能为空')
            }
            // 添加节点时检查节点名称是否存在
            const children = tag.children
            for (const c of children) {
                if (childTagName === c.name && c.name !== '-') {
                    return this.warn('当前节点名称已存在')
                }
            }
            this.chooseTag = Basic.append(tag, this.tagType, childTagName)
        }
        this.appendCancel()
    }

    private appendCancel(): void {
        this.chooseTag = null
        this.superTagName = ''
        this.newTagName = ''
        this.tagType = BaseType.Object
        this.tagTypeDisable = false
        this.clearAllRender()
        this.dialogFormVisible = false
    }

    private appendChooseSelect(item: BaseStruct): void {
        // 判断如果类型不存在则为默认string
        // 如果类型不存在items存在则为Object
        if (!this.chooseTag) {
            return
        }
        const items = this.chooseTag.items
        if (!items) {
            return
        }
        let s: BaseStruct | null = null
        for (const v of items) {
            if (item.name === v.name) {
                s = v
                break
            }
        }
        if (!s) {
            return
        }

        this.tagType = BaseType.String
        this.tagTypeDisable = true
        if (s.type) {
            this.tagType = <BaseType>s.type
            return
        }
        if (s.items) {
            this.tagType = BaseType.Object
        }
    }

    private querySearch(queryString: string, cb: Function): void {
        const results = queryString ? this.restaurants.filter((restaurant: BaseND) => {
            // TODO queryString 为数字时没有小写
            return (restaurant.name.toLocaleLowerCase().indexOf(queryString.toLocaleLowerCase()) === 0)
        }) : this.restaurants
        cb(results) // 调用 callback 返回建议列表的数据
    }

    private remove(node: TreeNode<number, Point>, data: Point): void {
        const parent = node.parent
        if (parent === null) {
            return
        }
        const children = parent.data.children || parent.data
        const index = children.findIndex((d: Point) => d.id === data.id)
        children.splice(index, 1)
    }

    private removeShow(data: Point): boolean {
        return (!data.must || false) || data.name === '-'
    }

    private setValue(data: Point, node: TreeNode<number, Point>): void {
        // 获取节点，渲染可用提示值
        this.setValueRestaurants = Basic.setValueTip(data.tips || [])
        this.tagTitle = '请填写' + data.name + '内容'
        this.chooseTag = data
        this.chooseNode = node
        this.tagDialog = true
    }

    private setValueConfirm(): void {
        const tag = this.chooseTag
        const node = this.chooseNode
        if (tag !== null && node !== null) {
            tag.value = this.tagValue
            tag.label = tag.name + tag.mark + tag.value
            if (tag.name === KindPoint) {
                const parent = node.parent
                if (parent && parent.data.name === RootPoint) {
                    // 更换kind类型，将root节点替换为初始化状态，并选择指定的kind值
                    this.kindType = tag.value
                    const basePoints = Basic.getK8sNoRootPoints()
                    for (const v of basePoints) {
                        if (v.name === KindPoint) {
                            v.value = tag.value
                            v.label = tag.label
                            break
                        }
                    }
                    basePoints.push(...Basic.getResourcePoints(tag.value))
                    parent.data.children = basePoints
                }
            }
        }
        this.setValueCancel()
    }

    private setValueCancel(): void {
        this.chooseTag = null
        this.tagTitle = '请填写Tag值'
        this.tagValue = ''
        this.clearAllRender()
        this.tagDialog = false
    }

    private clearAllRender(): void {
        this.restaurants = []
        this.setValueRestaurants = []
    }

    private setValueQuerySearch(queryString: string, cb: Function): void {
        const results = queryString ? this.setValueRestaurants.filter((restaurant: BaseND) => {
            return (restaurant.name.toLocaleLowerCase().indexOf(queryString.toLocaleLowerCase()) === 0)
        }) : this.setValueRestaurants
        cb(results)
    }

    private appendArray(data: Point): void {
        this.chooseTag = data
        this.arrayTypeChooseDialog = true
    }

    private appendArrayConfirm(): void {
        if (this.chooseTag !== null) {
            this.chooseTag.arrayType = this.arrTagType
            this.chooseTag.items = []
            this.chooseTag.items.push({ name: '-', value: '', desc: "", type: this.arrTagType })
            this.chooseTag = Basic.append(this.chooseTag, this.arrTagType, '-')
        }
        this.appendArrayCancel()
    }

    private appendArrayCancel(): void {
        this.arrTagType = BaseType.String
        this.arrayTypeChooseDialog = false
    }

    private warn(message: string = '出错啦'): void {
        this.$message({
            message: message,
            type: 'warning'
        })
    }

    private buildYaml(): void {
        if (this.kindType === '') {
            return this.warn('请先添加kind属性')
        }
        let set: Point[] = []
        let yamlStr = ''
        for (const v of this.data) {
            if (v.name === RootPoint) {
                if (yamlStr !== '') {
                    yamlStr += `---\n`
                }
                yamlStr += Basic.convertToYaml(v.children)
            }
        }
        this.yamlData = yamlStr
    }

    private parseYaml(): void {
        try {
            const data = Basic.getRootPoint()
            data.children = Basic.convertToTree(this.yamlData)
            this.kindType = Basic.getTreeKind(data.children)
            this.data = [data]
        } catch (e) {
            this.parseMultiYaml()
        }
    }

    private parseMultiYaml(): void {
        try {
            this.data = Basic.convertToTreeMulti(this.yamlData)
        } catch (e) {
            this.warn("yaml解析异常")
        }
    }

    private addPoint(): void {
        this.data.push(...Basic.getK8sRootPoints())
    }

    private copyYaml(): void {
        if (this.kindType === '') {
            return this.warn('请先添加kind属性')
        }
        if (this.yamlData === '') {
            return this.warn('请先生成yaml')
        }
        var text = document.createElement("textarea");    // 直接构建textarea
        text.value = this.yamlData                                 // 设置内容
        document.body.appendChild(text);                           // 添加临时实例
        text.select();                                             // 选择实例内容
        document.execCommand("Copy");                   // 执行复制
        document.body.removeChild(text);                           // 删除临时实例
    }

    private downloadYaml(): void {
        if (this.kindType === '') {
            return this.warn('请先添加kind属性')
        }
        if (this.yamlData === '') {
            return this.warn('请先生成yaml')
        }
        const element = document.createElement('a');
        element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(this.yamlData));
        const filename = this.kindType.toLowerCase() + '.yml'
        element.setAttribute('download', filename);
        element.style.display = 'none';
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
    }
}
