export enum BaseType {
    String = 'string',      // 字符串
    Number = 'number',      // 数值
    Boolean = 'boolean',    // 布尔
    Array = 'array',        // 数组
    Object = 'object',      // 对象
}

export interface BaseND {
    name: string
    desc: string
}

/*
    默认类型为string，当items存在时默认为Object
    指定类型为array时，当items存在时为arrayObject
 */
export interface BaseStruct {
    name: string            // 名称
    desc: string            // 描述
    value?: string          // 默认值
    must?: boolean          // 是否必须存在，默认false
    type?: string           // 值类型，默认string
    arrayType?: BaseType    // 数组子项类型
    tips?: BaseND[]         // 值提示内容
    items?: BaseStruct[]    // 子项
}

export interface Point extends BaseStruct {
    id: number
    label: string
    mark: string
    children: Point[]
}

const RootPoint: string = 'root'
const KindPoint: string = 'kind'
export {RootPoint, KindPoint}
